.alert-message-box{
    position: relative;
    margin-bottom: 10px;
    display:flex;
}

.alert-message-box .close-icon {
    position: absolute;
    right: 10px;
    top: 40%;
    color:black;
}

.alert-message-box .close-icon:hover{
    cursor: pointer;
}

.bg-success{
  
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}

.bg-error {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}

.bg-info{
    color: #0c5460;
    background-color: #d1ecf1;
    border-color: #bee5eb;
}

.bg-warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba;
}

.message-icon {
    position: relative;
    padding-left: 10px;
}



.message-icon span {
    position: absolute;
    top:40%
}

.message-list li {
    list-style: none;
    padding-bottom: 2px;
}